/* Skeleton */

.skeleton {
  background: #ddd;
  margin: 10px 0;
  border-radius: 4px;
  max-width: 100%;
}

.skeleton.text {
  width: 100%;
  height: 12px;
}

.skeleton.title {
  width: 50%;
  height: 20px;
  margin-bottom: 15px;
}

.skeleton.avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.skeleton.thumbnail {
  width: 100px;
  height: 100px;
}

.skeleton.sty {
  width: 100%;
}

.skeleton.button-sty {
  width: 250px;
  height: 65px;
  margin-right: 50px;
  background: #ddd;
}

.skeleton-wrapper {
  border-radius: 4px;
  position: relative;
  overflow: hidden;
}

.shimmer-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: loading 2.5s infinite;
}
.skeleton.mt-3 {
  margin-top: 1rem;
}

.skeleton.styled {
  width: 150px;
}

.skeleton.styled-child {
  width: 250px;
}

.skeleton.button {
  width: 350px;
  padding: 0.375rem 0.75rem;
  height: 30px;
}

.skeleton-wrapper-child {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 350px;
  margin: auto;
  width: 430px;
  border-radius: 4px;
  max-width: 100%;
  background: #f2f2f2;
}

.skeleton.input {
  width: 350px;
  height: 45px;
  background: #ddd;
}

.skeleton.banner {
  width: 500px;
  height: 500px;
}

.shimmer {
  width: 50%;
  height: 100%;
  background: rgba(255, 255, 255, 0.2);
  transform: skewX(-20deg);
  box-shadow: 0 0 30px 30px rgba(255, 255, 255, 0.05);
}

.skeleton.list {
  padding: 20px;
}

@keyframes loading {
  0% {
    transform: translateX(-150%);
  }
  50% {
    transform: translateX(-60%);
  }
  100% {
    transform: translateX(150%);
  }
}

.alert {
  position: fixed;
  z-index: 999;
  width: 320px;
  max-width: 100%;
  padding: 10px;
  top: 0;
  user-select: none;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgb(231, 232, 233);
  box-shadow: 0px 0px 6px #888888;
  color: #000;
  animation: alert 1s;
}

@keyframes alert {
  0% {
    transform: translate(-50%, -300%);
  }
  100% {
    transform: translate(-50%, 0);
  }
}

.alert-title {
  margin-left: 55px;
  margin-top: 4px;
  font-size: 14px;
  font-weight: bold;
  text-transform: capitalize;
  line-height: 18px;
}

.alert-text {
  margin-left: 55px;
  margin-top: 5px;
  font-size: 10px;
  line-height: 15px;
}

.alert-button-container {
  padding-top: 10px;
  padding-right: 10px;
  padding-bottom: 10px;
  float: right;
}

.alert-btn-not {
  background: linear-gradient(to bottom, #ffffff 5%, #f6f6f6 100%);
  border-radius: 6px;
  border: 1px solid #dcdcdc;
  color: #666;
  display: inline-block;
  font-size: 12px;
  padding: 4px 24px;
  margin-right: 6px;
  cursor: default;
}

.alert-btn-agree {
  display: inline-block;
  background: linear-gradient(to bottom, #33bdef 5%, #019ad2 100%);
  border-radius: 6px;
  color: #fff;
  border: 1px solid #057fd0;
  font-size: 12px;
  padding: 4px 24px;
  cursor: default;
}
